import React from 'react';
import { PDFDocument, rgb } from 'pdf-lib';
import { PDFViewer } from '@react-pdf-viewer/core';
import { pdfjs } from 'react-pdf';
import './PDFGenerator.css';

const PDFGenerator = ({ children, userID, certificateID }) => {
  const handleDownload = async () => {
    try {
      const pdfDoc = await PDFDocument.create();
      const page = pdfDoc.addPage([600, 400]);
      const { width, height } = page.getSize();

      const fontSize = 30;
      page.drawText('Your PDF Content', {
        x: 50,
        y: height - fontSize - 50,
        size: fontSize,
        color: rgb(0, 0, 0),
      });

      // Add logic to render children components here
      // You can use positioning or layout libraries for better control

      const pdfBytes = await pdfDoc.save();
      const blob = new Blob([pdfBytes], { type: 'application/pdf' });
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = `${userID}-${certificateID}.pdf`;
      link.click();
    } catch (error) {
      console.error('Error generating PDF:', error);
      // Handle the error gracefully, like displaying an error message
    }
  };

  return (
    <div style={{ position: 'relative' }}>
      {children}
      <button
        onClick={handleDownload}
        style={{
         height:'50px',
         width:'100px',
         position:'absolute',
         alignItems:'center',
         
        }}
      >
        Download PDF
      </button>
    </div>
  );
};

export default PDFGenerator;